import axios from 'axios'
// import { MEDIA_API_URL } from '../../env'
import { getSessionToken } from '../session'

const MEDIA_API_URL = process.env.REACT_APP_MEDIA_API_URL

async function uploadFileRequest(filePath, file) {
    const fileHeaders = {
        'Content-Type': file.type || 'application/octet-stream',
        'Authorization': `Bearer ${getSessionToken()}`
    }
    const uploadUrl = `${MEDIA_API_URL}/${filePath}`
    return axios
        .put(uploadUrl, file, { headers: fileHeaders })
            .then(res => res.data.name || res.data)
}

export async function uploadFile(file, groupId = 0) {
    return uploadFileRequest(groupId ? `${groupId}/${file.name}` : `${file.name}`, file)
}

export const getImageUrl = path => {
    if (path && !path.match(/^https?:\/\//)) {
        return MEDIA_API_URL + '/' + path
    }

    return path
}

export const handleImageUploadIfNeeded = async (imageFile, groupId) => {
    if (imageFile && typeof imageFile !== 'string') {
        const image = await uploadFile(imageFile, groupId)
        return getImageUrl(image)
    }

    return imageFile
}
